

import Vue from 'vue'
import App from './App'
import router from './router'
import service from "./bin/fetch";
import wxapi from "./bin/wxapi"
import settitle from "./assets/Tool/settitle"
import "./assets/css/public.css"
import we from "./bin/weChat_jsSdk.js"
import sha1 from "./bin/jquery.sha1"
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

import Toast from 'vux/src/components/toast/index.vue'

import { Scroller } from '@nutui/nutui';

Scroller.install(Vue);
Vue.use(ToastPlugin, { position: 'middle' })
Vue.use(sha1);
Vue.use(we);
Vue.use(LoadingPlugin, { position: 'absolute' })
Vue.config.productionTip = false
Vue.prototype.$fetch = service;
Vue.prototype.wxapi = wxapi;
Vue.prototype.toNumber = function (num) {
return Number(num).toFixed(1);
}
Vue.component('toast', Toast)
window.settitle = settitle;

router.beforeEach((to, from, next) => {
if(!localStorage.getItem('user') && to.name !== "login" && to.name !== "shop" && to.name !=="home"){
next("/login/1");
return
}
next();
})
/* eslint-disable no-new */
new Vue({
el: '#app',
router,
render: h => h(App),
}).$mount('#app')



