
import url from "../../bin/url";
import Datetime from 'vux/src/components/datetime/index.vue'

export default {
components: {
Datetime
},
name: "dataStatistics",
data() {
return {
tabList: [
{
name: "今天",
id: "0"
},
{
name: "昨天",
id: "1"
},
{
name: "近7天",
id: "2"
},
{
name: "近30天",
id: "3"
},
{
name: "自定义",
id: "4"
}
],
switchFlage: "0", //默认展示tab
valueS: "2019-09-09",
valueE: "2020-09-09",
moneyList: ""
};
},
computed: {
type() {
return localStorage.getItem("type");
}
},
methods: {

tabSwitch(id) {
console.log(id);
this.switchFlage = id;
this.getStatistics(); 
},

goTODataDetails(item) {
console.log(item)
this.$router.push({
name: "dataDetails",
params: {
obj: JSON.stringify({
type: "profession",
data: {
id: item
}
})
}
});
},

changeS(value) {
console.log("change", value);
},
changeE(value) {
console.log("change", value);
},

setToday(value) {
console.log(value);
let now = new Date();
let cmonth = now.getMonth() + 1;
let day = now.getDate();
if (cmonth < 10) cmonth = "0" + cmonth;
if (day < 10) day = "0" + day;
this.value1 = now.getFullYear() + "-" + cmonth + "-" + day;
console.log(this.value1);
},

getStatistics() {
let _obj = {
openId: url.openId,
flag: this.switchFlage,
startTime: this.valueS,
endTime: this.valueE
};
this.$fetch.post(url.getStatistics, _obj).then(data => {
if (data.code == 0) {
this.moneyList = data.obj;
}
});
}
},
created() {
settitle("数据统计");
this.routeParams = JSON.parse(this.$route.params.obj);
},

mounted() {
this.getStatistics(); 
console.log("数据统计");
}
};



