import url from "../../bin/url";
export default {
  name: "allMembersDetails",
  data() {
    return {
      phone: "",
      allOrderList: {
        name: "詹某某",
        phone: "18600000000",
        orderLists: ''
      }
    };
  },
  methods: {
    //   去详情
    goTOallMembersDD(id) {
      console.log(id);
      this.$router.push({
        name: "allMembersDetailsD",
        params: {
          obj: JSON.stringify({
            type: "profession",
            data: {
              id: id
            }
          })
        }
      });
    },
    // 查询订单会员
    getAllOrder() {
      let keywords = {
        openId: url.openId,
        id: this.routeParams.data.id
      };
      this.$fetch.post(url.getAllOrder, keywords).then(
        data => {
          console.log(data);
          if (data.code == 0) {
            this.allOrderList = data.obj;
          }
        },
        err => {
          alert("网络缓慢。。");
        }
      );
    }
  },
  created() {
    settitle("全部会员详情");
    this.routeParams = JSON.parse(this.$route.params.obj);
    console.log(this.routeParams)
  },

  mounted() {
    //获取列表
    this.getAllOrder();
  }
};



