
import url from "../../bin/url";
import wexinPay from '../../bin/weiXinPay'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

export default {
name: "orderDetails",
components: {
XTextarea
},
data() {
return {
key: "value",
selectAddressN: "",
postFee: "",
goodsMsg: {},

shopOrderList: "" 
};
},
computed: {},
methods: {

getShopOrder(item) {
let _obj = {
openId: url.openId,
id: item
};
this.$fetch.post(url.getShopOrder, _obj).then(data => {
if (data.code == 0) {
data.obj.goodsList.forEach(item => {
item.picId = url.imgUrl + item.picId;
});

this.shopOrderList = data.obj;
}
});
},

payOrder() {
let _obj = {
openId: url.openId,
id: this.routeParams.data.id
};
this.$fetch.post(url.payOrder, _obj).then(data => {
if (data.code == 0) {
var obj = eval("(" + data.obj + ")");
console.log(obj)
wexinPay(obj)
}
});
}
},
mounted() {
console.log(wexinPay)
},
created() {
if (this.$route.params.obj) {
this.routeParams = JSON.parse(this.$route.params.obj);
console.log(this.routeParams.data.id);
this.getShopOrder(this.routeParams.data.id); 
}
}
};






