
import url from "../../bin/url";
import TabBar from "../../components/TabBar";
import Confirm from 'vux/src/components/confirm/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'


export default {
components: {
TabBar,
Confirm,
XDialog,
Datetime
},
name: "newOrder",
data() {
return {
item: 0,
newFalge: false, //是否新建
newPay: true, //新建订单支付
classA: 0, //选择标示
payShowD: false, //支付
miMa: "",
recipeList: [], //会员下拉框数据
memberRecipe: "", //配方详情
phone: "", //会员手机号
memberID: "", //会员套餐id
position: "middle", //弹出框类型
showPositionValue: false, //弹出框
checkCustomerName: "", //姓名
value7: "", //开始时间
startDate: "2015-11-11", //时间控制
startTime: "请选择", //开始日期
endTime: "", //结束日期
memberIDNumber: "",       payMoney: "", //金额
selected: "",
passwordNumber: "", //支付密码
msgPAW: "",
msgLength: 0,
msg: ""
};
},
watch: {
msgPAW(curVal) {
console.log(curVal);
if (/[^\d]/g.test(curVal)) {
this.msgPAW = this.msgPAW.replace(/[^\d]/g, "");
} else {
this.msgLength = curVal.length;
this.msg = curVal;
}
}
},
methods: {
focus() {
this.$refs.pwd.focus();
},

change(value) {
this.startTime = value;
console.log(this.memberID);
dateTemp = value;
var dateTemp = dateTemp.split("-");
var nDate = new Date(dateTemp[1] + "-" + dateTemp[2] + "-" + dateTemp[0]); 
var millSeconds =
Math.abs(nDate) + this.memberIDNumber * 24 * 60 * 60 * 1000;
var rDate = new Date(millSeconds);
var year = rDate.getFullYear();
var month = rDate.getMonth() + 1;
if (month < 10) month = "0" + month;
var date = rDate.getDate();
if (date < 10) date = "0" + date;
this.endTime = year + "-" + month + "-" + date;
},

timeNow() {
var date = new Date();
var seperator1 = "-";
var year = date.getFullYear();
var month = date.getMonth() + 1;
var strDate = date.getDate();
if (month >= 1 && month <= 9) {
month = "0" + month;
}
if (strDate >= 0 && strDate <= 9) {
strDate = "0" + strDate;
}
this.startDate = year + seperator1 + month + seperator1 + strDate;
},
setToday(value) {
let now = new Date();
let cmonth = now.getMonth() + 1;
let day = now.getDate();
if (cmonth < 10) cmonth = "0" + cmonth;
if (day < 10) day = "0" + day;
this.value7 = now.getFullYear() + "-" + cmonth + "-" + day;
},

onCancel() {},


onConfirm() {

let _obj = {
openId: url.openId,
id: this.memberID, //会员套餐id
phone: this.phone
};
this.$fetch.post(url.saveMemberRecipe, _obj).then(
data => {
if (data.code == 0) {
this.$router.push({
name: "orderSuccess",
params: {
obj: JSON.stringify({
type: "profession",
data: {
id: "参数"
}
})
}
});
}
},
err => {
alert("网络缓慢。。");
}
);

this.newPay = false; 
},

payPassW() {
this.payShowD = true;
},

payPassWGB() {
this.payShowD = false;
this.passwordNumber = DesUtils.encode(this.msg, "fruits-app,yuntu,com");

let _obj = {
openId: url.openId,
id: this.selected.id,
phone: this.phone,
password: this.passwordNumber,
startDate: this.startTime
};
this.$fetch.post(url.saveInediaRecipe, _obj).then(
data => {
if (data.code == 0) {
console.log("7897907");
}
},
err => {
alert("网络缓慢。。");
}
);
},














getCheckCustomer(phone) {
if (this.phone.length != 11) {
return;
}
let _obj = {
openId: url.openId,
phone: phone
};
this.$fetch.post(url.checkCustomer, _obj).then(
data => {
if (data.code == 0) {
this.checkCustomerName = data.obj.name;
}
},
err => {
alert("网络缓慢。。");
}
);
},

newOrderXZ(falge) {
this.classA = falge;

let _obj = {
openId: url.openId,
type: falge
};
console.log(url.getRecipe);
this.$fetch.post(url.getRecipe, _obj).then(
data => {
if (data.code == 0) {
this.recipeList = data.obj;
}
},
err => {
alert("网络缓慢。。");
}
);
},

getMemberRecipe(event) {
this.memberID = event.target.value;
let _obj = {
openId: url.openId,
id: this.memberID
};
this.$fetch.post(url.getMemberRecipe, _obj).then(
data => {
if (data.code == 0) {
this.memberRecipe = data.obj;
}
},
err => {
alert("网络缓慢。。");
}
);
},

getMemberRecipeDay() {
this.memberID = this.selected.id;
this.memberIDNumber = this.selected.recipe;
this.payMoney = this.selected.retail;
},

newOrderq() {
if (this.phone.length != 11) {
this.showPositionValue = true;
return;
}
this.newFalge = true;
}
},
created() {
settitle("新建订单");
this.routeParams = JSON.parse(this.$route.params.obj);
},

mounted() {
console.log("新建订单");
this.timeNow(); 
}
};



