import url from "../../bin/url";
export default {
  name: "dataDetails",
  data() {
    return {
      dataChargeList: [],
      page: {
        size: 10,
        current: 1
      },
      isUnMore1: false,
      isLoading1: false,
      dataChargeListP: ""
    };
  },
  methods: {
    // 获取数据
    getChargeList() {
      let _obj = {
        openId: url.openId,
        type: this.routeParams.data.id,
        size: this.page.size,
        current: this.page.current
      };
      this.$fetch.post(url.getChargeList, _obj).then(data => {
        if (data.code == 0) {
          this.dataChargeList = this.dataChargeList.concat(data.obj);
        }
      });
    },
    // 上拉加载
    selPullUp() {
      this.page.current++;
      this.getChargeList();
    },
    // 下拉刷新
    pulldown() {
      this.isUnMore1 = false;
      this.page.current = 1;
      this.dataChargeList =[];
      this.getChargeList();
    }
  },
  created() {
    settitle("我是登陆页面");
    this.routeParams = JSON.parse(this.$route.params.obj);
    console.log(this.routeParams.data.id);
  },

  mounted() {
    this.getChargeList(); //获取数据
  }
};



