
import url from "../../bin/url";
import Confirm from 'vux/src/components/confirm/index.vue'


export default {
components: {
Confirm
},
name: "recharge",
data() {
return {
classA: "",
rechargeFalge: false, //是否新建
count:3,
timer:null,
phone:null,
name:"",
countList:[],
form:{
openId:"1313121231",
type:"",
phone:null,
amount:null,
id:"",
payType:""
},
ifHas:false,//判断是否有手机号
ifChoic:false,
};
},
methods: {

moneyXz(falge,index) {
this.countList[index].log = !this.countList[index].log;
this.countList.forEach((e,i) =>{
if(i == index){
if(e.log){
this.ifChoic = true;
this.form.amount = null;
}else{
this.ifChoic = false;
}
return
}
e.log = false;
})

this.classA = falge;
this.form.id = falge;
},

goToRechargeList: function() {
if(this.ifHas){
this.$router.push({
name: "rechargeList",
params: {
obj: JSON.stringify({
type: "profession",
data: {
phone: this.phone
}
})
}
});
}else{
this.$vux.toast.text('请输入正确的手机号');
}

},

getCount(){
this.$vux.loading.show({
text: 'Loading'
})
this.$fetch.post('fruits/app/personal/getPupList',{openId:"1313121231"}).then(res =>{
console.log(res);
res.obj.forEach(item => {
item.log = false;
this.countList.push(item)
});
this.$vux.loading.hide();
})
},

cashMethod(){
this.$fetch.post("fruits/app/personal/remainRecharge",this.form).then(res =>{
console.log(res);
if(res.msg == "success"){
this.$vux.toast.text('充值成功');
}else{
if(res.msg =="cash_pledge"){
this.$vux.toast.text('押金不足');
return
}
this.$vux.toast.text('遇到了错误，请重试');
}
})
},

rechargeq(val) {
if(this.ifHas){
this.rechargeFalge = true;
this.form.payType = val;
this.form.type = this.form.amount? "1" : "0";
this.form.phone = this.phone;
}else{
this.$vux.toast.text('请输入正确的手机号');
}

},

onCancel() {

},

onConfirm() {
this.newPay = false;
this.cashMethod();
},
showMember(value){
console.log(1212)
this.$fetch.post('fruits/app/personal/checkCustomer',{openId:"1313121231",phone:value}).then(res =>{
this.$vux.loading.hide();
if(res.msg == 'find_none_user'){
this.$vux.toast.text('没有找到该用户');
}
if(res.msg == "user_has_frozen"){
this.$vux.toast.text('该用户已被冻结');

}
if(res.msg == "success"){
this.name = res.obj.name;
this.ifHas = true;
}

})
},

phoneSearch(){
var reg = /^1[3,4,5,6,7,8,9]\d{9}$/;
if( this.timer ){

clearTimeout(this.timer);
}
this.timer = setTimeout(() =>{
if(reg.test(this.phone)){
this.showMember(this.phone);
this.$vux.loading.show({
text: '查询中'
})
}else{
this.$vux.toast.text('请输入正确的手机号');
}
},2500)
}
},
created() {
settitle("押金充值");
this.routeParams = JSON.parse(this.$route.params.obj);
},

mounted() {
this.getCount();
}
};



