import url from "../../bin/url";
export default {
  name: "myWallet",
  data() {
    return {
      tabList: [
        {
          name: "全部",
          id: "0"
        },
        {
          name: "待付款",
          id: "1"
        },
        {
          name: "待发货",
          id: "2"
        },
        {
          name: "待发货",
          id: "3"
        },
        {
          name: "已完成",
          id: "4"
        }
      ],
      switchFlage: "0",
      orderList: "",
      page: {
        current: "1",
        size: "10"
      },
      isUnMore1: false,
      isLoading1: false
    };
  },
  methods: {
    // tab切换
    tabSwitch(id) {
      this.switchFlage = id;
      this.getOrderList(); //切换时获取数据
    },
    // 去支付
    goToPay() {
      // this.$router.push({
      //   name: "orderDetails",
      //   params: {
      //     obj: JSON.stringify({
      //       type: "profession",
      //       data: {
      //         id: "pay"
      //       }
      //     })
      //   }
      // });
    },
    goToOrderDetails(item) {
      this.$router.push({
        name: "orderDetails",
        params: {
          obj: JSON.stringify({
            type: "profession",
            data: {
              id: item
            }
          })
        }
      });
    },
    // 获取商城订单
    getOrderList() {
      let _obj = {
        openId: url.openId,
        state: this.switchFlage,
        size: this.page.size,
        current: this.page.current
      };
      this.$fetch.post(url.getOrderList, _obj).then(
        data => {
          if (data.code == 0) {
            data.obj.forEach(item => {
              item.goodsList.forEach(items => {
                items.picId = url.imgUrl + items.picId;
              });
            });
            this.orderList = data.obj;
          }
        },
        err => {
          alert("网络缓慢。。");
        }
      );
    },

    // 确认收货
    sureReceiving(item) {
      let _obj = {
        openId: url.openId,
        id: item.id
      };
      this.$fetch.post(url.sureReceiving, _obj).then(
        data => {
          if (data.code == 0) {
            console.log(data)
          }
        },
        err => {
          alert("网络缓慢。。");
        }
      );
    },
    // 上拉加载
    selPullUp() {
      this.page.current++;
      this.getOrderList();
    },
    // 下拉刷新
    pulldown() {
      this.isUnMore1 = false;
      this.page.current = 1;
      this.getOrderList();
    }
  },

  created() {
    settitle("我的订单");
    this.routeParams = JSON.parse(this.$route.params.obj);
  },

  mounted() {
    console.log("我的订单");
    this.getOrderList(); //获取商城订单
  }
};



