import url from "../../bin/url";
export default {
  name: "memberOperation",
  data() {
    return {
      memberList: [], //列表数据
      phone: "" //查询手机号
    };
  },
  methods: {
    //  去会员详情
    goToMemberD(item) {
      this.$router.push({
        name: "memberD",
        params: {
          obj: JSON.stringify({
            type: "profession",
            data: {
              item
            }
          })
        }
      });
    },
    // 去新建套餐
    goToNewOrder() {
      this.$router.push({
        name: "newOrder",
        params: {
          obj: JSON.stringify({
            type: "profession",
            data: {}
          })
        }
      });
    },
    // 去全部会员
    goToAllMembers() {
      this.$router.push({
        name: "allMembers",
        params: {
          obj: JSON.stringify({
            type: "profession",
            data: {}
          })
        }
      });
    },
    // 获取列表参数
    getMemberList() {
      let keywords = {
        openId: url.openId
      };
      this.$fetch.post(url.getMemberList, keywords).then(
        data => {
          console.log(data);
          if (data.code == 0) {
            this.memberList = data.obj;
          }
        },
        err => {
          alert("网络缓慢。。");
        }
      );
    },
    // 查询订单会员
    getCheckMember(phone) {
      if (phone.length != "11") {
        return;
      }
      let keywords = {
        openId: url.openId,
        phone: phone
      };
      this.$fetch.post(url.checkMember, keywords).then(
        data => {
          console.log(data);
          if (data.code == 0) {
            this.memberList = data.obj;
          }
        },
        err => {
          alert("网络缓慢。。");
        }
      );
    }
  },
  created() {
    settitle("会员操作");
    this.routeParams = JSON.parse(this.$route.params.obj);
  },

  mounted() {
    //获取列表
    this.getMemberList();
  }
};



