
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Badge from 'vux/src/components/badge/index.vue'

export default {
name: "TabBar",
components: {
Flexbox,
FlexboxItem,
Badge
},
computed: {
route() {
return this.$route.path;
}
},
props: {},
data: function() {
return {
actives: 0,
show: false,
buyNum: 0,
title:""
};
},
methods: {
ImmeBuy(word) {
this.show = true;
this.title = word;
},
addCart(word) {
this.show = true;
this.title = word;

},
didClickedItem: function(tag, name) {
this.actives = tag;
this.$router.push({
name: name,
params: {
obj: JSON.stringify({
type: "profession",
data: {
id: "参数"
}
})
}
});
},
setBage(){
this.buyNum = localStorage.getItem('catnum')
}
},
mounted() {
this.setBage();

}
};



