import url from "../../../bin/url";
export default {
  data() {
    return {
      key: "value",
      videoOrderList: ""
    };
  },
  methods: {
    // 获取列表
    getVideoOrder() {
      let _obj = {
        openId: url.openId
      };
      this.$fetch.post(url.getVideoOrder, _obj).then(
        data => {
          if (data.code == 0) {
            console.log(data);
            // this.videoOrderList = data.obj;
          }
        },
        err => {
          alert("网络缓慢。。");
        }
      );
    }
  },
  mounted() {
    this.getVideoOrder();
  }
};



