import url from "../../bin/url";
// import func from "../../../vue-temp/vue-editor-bridge";
export default {
  name: "serviceOrder",
  data() {
    return {};
  },
  methods: {
    goToSOrderD(falg) {
      this.$router.push({
        name: "serviceOrderD",
        params: {
          obj: JSON.stringify({
            type: "profession",
            data: {
              id: falg
            }
          })
        }
      });
    }
  },
  created() {
    settitle("健康界的轻奢");
    this.routeParams = JSON.parse(this.$route.params.obj);
    console.log(this.routeParams);
  },

  mounted() {
    console.log("服务订单");
  }
};



