
import url from "../../bin/url";
import TabBar from "../../components/TabBar";
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

export default {
components: {
CheckIcon,
Confirm,
TabBar
},
name: "nationalStor",
data() {
return {
item: 0,
isDefault: "",
classA: "1", //是否有单选框
nationSFalg: false, //地址确认
nationList: [
{ isDefault: false },
{ isDefault: true },
{ isDefault: false }
],
nationQList: [
{ isQDefault: false },
{ isQDefault: true },
{ isQDefault: false }
],
page: {
current: "1",
size: "10"
},
recommendStoreList: "",
productNamr: "",
isUnMore1: false,
isLoading1: true
};
},
methods: {
goToMap() {
console.log("7897");
this.$router.push({
name: "Tmap",
params: {
obj: JSON.stringify({
type: "profession",
data: {
id: "参数"
}
})
}
});
},

checkBtn(index, id) {

this.nationList.forEach(item => {
item.isDefault = false;
});

this.radio = this.nationList[index].isDefault;

this.nationList[index].isDefault = true;
console.log(this.radio);









},

checkQBtn(index, id) {

this.nationQList.forEach(item => {
item.isQDefault = false;
});

this.radio = this.nationQList[index].isQDefault;

this.nationQList[index].isQDefault = true;
console.log(this.radio);
this.nationq();
},

nationq() {
this.nationSFalg = true;
},

onCancel() {
console.log("2");
},

onConfirm() {
console.log("1");
},

getRecommendStoreList(item) {
let _obj = {
openId: url.openId,
name: item || '',
size: this.page.size,
current: this.page.current
};
this.$fetch.post(url.getRecommendStoreList, _obj).then(
data => {
if (data.code == 0) {
this.recommendStoreList = data.obj;
console.log(this.recommendStoreList);
}
},
err => {
alert("网络缓慢。。");
}
);
},

selPullUp() {
this.page.current++;
this.getRecommendStoreList();
},

pulldown() {
this.isUnMore1 = false;
this.page.current = 1;
this.getRecommendStoreList();
}
},
created() {
settitle("我是全国们店");

},

mounted() {
this.getRecommendStoreList(); 
}
};



