import url from "../../bin/url";
export default {
  name: "healthBonus",
  data() {
    return {
      tabList: [
        {
          name: "推荐返佣",
          id: "001"
        },
        {
          name: "视频返佣",
          id: "002"
        },
        {
          name: "商城返佣",
          id: "003"
        }
      ],
      switchFlage: "001",
      getRecommendBList: "", //推荐返佣
      getVideoDistriList: "", //视频返佣
      getOrderDistriList: "", //订单返佣
      getHealthBonusD: "" //返佣
    };
  },
  methods: {
    //   tab切换
    tabSwitch(id) {
      switch (id) {
        case "001":
          this.getRecommendB();
          break;
        case "002":
          this.getVideoDistri();
          break;
        case "003":
          this.getOrderDistri();
          break;
      }
      this.switchFlage = id;
    },
    // 健康奖金详情
    goToHealthDetail() {
      this.$router.push({
        name: "healthDetail",
        params: {
          obj: JSON.stringify({
            type: "profession",
            data: {
              id: this.switchFlage
            }
          })
        }
      });
    },
    // 活动规则
    goToActivityRules() {
      this.$router.push({
        name: "activityRules",
        params: {
          obj: JSON.stringify({
            type: "profession",
            data: {
              id: "参数"
            }
          })
        }
      });
    },
    // 健康奖金金额统计
    getHealthBonus() {
      let _obj = {
        openId: url.openId
      };
      this.$fetch.post(url.getHealthBonus, _obj).then(
        data => {
          if (data.code == 0) {
            this.getHealthBonusD = data.obj;
          }
        },
        err => {
          alert("网络缓慢。。");
        }
      );
    },
    // 健康奖金--推荐返佣列表
    getRecommendB() {
      let _obj = {
        openId: url.openId,
        name: "",
        size: "10",
        current: "1"
      };
      this.$fetch.post(url.getRecommendB, _obj).then(
        data => {
          if (data.code == 0) {
            this.getRecommendBList = data.obj;
          }
        },
        err => {
          alert("网络缓慢。。");
        }
      );
    },
    // 视频返佣列表
    getVideoDistri() {
      let _obj = {
        openId: url.openId,
        name: "",
        size: "10",
        current: "1"
      };
      this.$fetch.post(url.getVideoDistri, _obj).then(
        data => {
          if (data.code == 0) {
            this.getVideoDistriList = data.obj;
          }
        },
        err => {
          alert("网络缓慢。。");
        }
      );
    },
    // 健康奖金商城
    getOrderDistri() {
      let _obj = {
        openId: url.openId,
        name: "",
        size: "10",
        current: "1"
      };
      this.$fetch.post(url.getOrderDistri, _obj).then(
        data => {
          if (data.code == 0) {
            this.getOrderDistriList = data.obj;
          }
        },
        err => {
          alert("网络缓慢。。");
        }
      );
    }
  },
  created() {
    settitle("健康奖金");
    this.routeParams = JSON.parse(this.$route.params.obj);
  },

  mounted() {
    // console.log("我是健康奖金");
    this.getHealthBonus();
    this.getRecommendB();
  }
};



