
import url from "../../bin/url";
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: "login",
components: {
XInput,
XButton
},
computed: {
haslogin() {
return this.$route.params.id;
}
},
data() {
return {
disVal: "",
btn: "注册",
style: "",
timer: "",
btnload: false,
maskValue: "",
codeValue: "获取验证码",
form: {
openId: "",
password: "",
name: "",
phone: "",
code: "",
headimgurl:
"http://m.imeitou.com/uploads/allimg/2019021309/ipijc3xjpfo.jpg",
nickname: "随便"
},
Logform: {
openId: "",
password: "",
phone: "13600000000",
nickname: "掌声",
headimgurl: "http://m.imeitou.com/uploads/allimg/2019021309/ipijc3xjpfo.jpg"
},

validTel: false
};
},
methods: {
clear() {},
change() {
console.log(123);
},
LoginOrReg() {
this.haslogin == 1 ? this.Login() : this.Reg();
},


Login() {
if (!this.$refs.inputTel.valid || !this.$refs.inputPas.valid) {
this.$vux.toast.text("请检查输入的内容");
return;
}
this.btnload = true;

this.Logform.password = DesUtils.encode(
this.Logform.password,
"fruits-app,yuntu,com"
);

this.$fetch.post("fruits/app/user/login", this.Logform).then(res => {
this.btnload = false;
if (res.msg == "success") {
this.$vux.toast.text("登录成功");
localStorage.setItem("user", res.attributes.sessionId);
localStorage.setItem("type", res.attributes.type);
localStorage.setItem("appUserId", res.attributes.appUserId); 
this.getCartNum();
} else {
this.$vux.toast.text("登录时出现问题，请重新登录");
}
});
},

Reg() {
if (
!this.$refs.inputTel.valid ||
!this.$refs.name.valid ||
!this.$refs.inputPas.valid ||
!this.$refs.code.valid
) {
this.$vux.toast.text("请检查输入的内容");
return;
}
this.loginP = this.form.password;
this.form.password = DesUtils.encode(
this.form.password,
"fruits-app,yuntu,com"
);
this.$fetch.post("fruits/app/user/register", this.form).then(res => {
if (res.msg == "registered") {
this.$vux.toast.text("手机号已经被注册");
return;
}
if (res.msg == "success") {
this.$vux.toast.text("注册成功");




if (res.attributes.type == 1) {
let _obj = {
openId: "",
password: DesUtils.encode(this.loginP, "fruits-app,yuntu,com"),
phone: this.form.phone
};
this.$fetch.post("fruits/app/user/login", _obj).then(res => {
this.btnload = false;
if (res.msg == "success") {
this.$vux.toast.text("登录成功");
localStorage.setItem("user", res.attributes.sessionId);
localStorage.setItem("type", res.attributes.type);
localStorage.setItem("appUserId", res.attributes.appUserId); 
this.$router.push("/home");
} else {
this.$vux.toast.text("登录时出现问题，请重新登录");
}
});
}
} else {
this.$vux.toast.text("出现错误，请重试");
}
});
},
editPass() {
this.$router.push("/changePassword/:obj", { obj: 1 });
},
regTest() {
this.$router.push("/login/2");
},

sendCode() {
var validTel = this.$refs.inputTel.valid;
console.log(validTel);
if (this.timer || !this.form.phone || !validTel) {
return;
}
var count = 10;
this.$fetch
.post("fruits/app/user/getSmsCode", {
openId: this.form.openId,
phone: this.form.phone
})
.then(res => {
this.form.code = "1234";
});

this.timer = setInterval(() => {
if (count <= 1) {
clearInterval(this.timer);
this.codeValue = "重新获取验证码";
this.timer = "";
return;
}
count--;
this.codeValue = count;
}, 1000);
},

getCartNum() {
this.$fetch
.post("fruits/app/cart/getCartNum", { openId: url.openId })
.then(res => {
console.log(res);
if (res.msg == "success") {
this.$router.push("/home");
localStorage.setItem("catnum", res.obj);
}
});
},

getOpenId() {
let _obj = {
code: "",
state: ""
};
this.$fetch.post(url.getOpenId, _obj).then(
data => {
if (data.code == 0) {
console.log("woshiopenid" + data);
}
},
err => {
alert("网络缓慢。。");
}
);
}
},
created() {
settitle("我是登录页面");

},
created() {
settitle("注册与登录");
},
mounted() {

if (this.$route.query["parm"]) {
var obj = JSON.parse(this.$route.query["parm"]);
this.Logform.phone = obj.phone;
this.Logform.password = obj.password;
this.Logform.openId = obj.openId;
}
}
};



