import url from "../../bin/url";
export default {
  name: "myWallet",
  data() {
    return {};
  },
  methods: {
    goToBillRecord() {
      this.$router.push({
        name: "billRecord",
        params: {
          obj: JSON.stringify({
            type: "profession",
            data: {
              id: "参数"
            }
          })
        }
      });
    }
  },
  created() {
    settitle("我的钱包");
