import url from "../../bin/url";
export default {
  name: "allMembers",
  data() {
    return {
      phone: "",
      allUserList:{
        attributes:{
          total:''
        }
      },
    };
  },
  methods: {
    // 查询订单会员
    getAllUser(item) {
      let keywords = {
        openId: url.openId,
        phone:item
      };
      this.$fetch.post(url.getAllUser, keywords).then(
        data => {
          console.log(data);
          if (data.code == 0) {
            this.allUserList = data;
          }
        },
        err => {
          alert("网络缓慢。。");
        }
      );
    },
    // 去全部会员详情
    goToDetails(id) {
      this.$router.push({
        name: "allMembersDetails",
        params: {
          obj: JSON.stringify({
            type: "profession",
            data: {
              id: id
            }
          })
        }
      });
    },
  },
  created() {
    settitle("全部会员");
    this.routeParams = JSON.parse(this.$route.params.obj);
  },

  mounted() {
    //获取列表
    this.getAllUser();
  }
};



