import url from "../../../bin/url";
export default {
  data() {
    return {
      searchList: [
        {
          videoid: "",
          videoimg: "",
          videoname: "王阳案例1",
          videodes: "查看此视频对糖尿病患者有帮助"
        },
        {
          videoid: "",
          videoimg: "",
          videoname: "王阳案例1",
          videodes: "查看此视频对糖尿病患者有帮助"
        }
      ],
      videoId: ""
    };
  },
  methods: {
    // 去视频列表
    visitTv(item) {
      this.$router.push({
        path: "/videopage",
        query: {
          obj: JSON.stringify({
            type: "profession",
            data: {
              id: item
            }
          })
        }
      });
    },
    // 获取列表
    getVideoTwo() {
      let _obj = {
        openId: url.openId,
        id: this.videoId
      };
      this.$fetch.post(url.getVideoTwo, _obj).then(
        data => {
          if (data.code == 0) {
            this.searchList = data.obj;
          console.log(this.searchList);
            
          }
        },
        err => {
          alert("网络缓慢。。");
        }
      );
    }
  },
  mounted() {
    this.getVideoTwo();
  },
  created() {
    if (this.$route.query.obj) {
      this.routeParams = JSON.parse(this.$route.query.obj);
      this.videoId = this.routeParams.data.id;
    }
  }
};



