import url from "../../bin/url";
export default {
  name: "allMembersDetailsD",
  data() {
    return {
      orderByIdList: "",
      ordertypeF: "",
      orderByIdListHY:{
        obj:{
          recipe:'',
          num:'',
          time:'',
          price:'',
          state:'',
          disease:''
        }
      },
      faly:''

    };
  },
  methods: {
    // 查询订单会员
    getOrderById() {
      let keywords = {
        openId: url.openId,
        id: this.routeParams.data.id
      };
      this.$fetch.post(url.getOrderById, keywords).then(
        data => {
          if (data.code == 0) {
            this.ordertypeF = data.attributes.type;
            if (this.ordertypeF == 1) {
              this.orderByIdList = data;
            } else {
              this.orderByIdListHY = data;
              this.faly = data.obj.state
            }

            console.log(this.orderByIdList);
          }
        },
        err => {
          alert("网络缓慢。。");
        }
      );
    },
    goPayB(id) {
      let _obj = {
        openId: url.openId,
        id: id
      };
      this.$fetch.post(url.sureBigu, _obj).then(
        data => {
          if (data.code == 0) {
          } else {
            alert(data.msg);
          }
        },
        err => {
          alert("网络缓慢。。");
        }
      );
    }
  },
  created() {
    settitle("全部会员详情");
    this.routeParams = JSON.parse(this.$route.params.obj);
    console.log(this.routeParams.id);
  },

  mounted() {
    //获取列表
    this.getOrderById();
  }
};



