
import url from "../../bin/url";
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
components: {
Scroller
},
name: "healthDetail",
data() {
return {
getOrderDistriList: "",
getRecommendBList: "",
getVideoDistriList: "",
name: "",
isUnMore1: false,
isLoading1: false,
page: {
size: 10,
current: 1
}
};
},
methods: {

getWholeList(item) {
this.itemName = item;
this.getListF();
},

getRecommendB() {
let _obj = {
openId: url.openId,
name: this.itemName,
size: this.page.size,
current: this.page.current
};
this.$fetch.post(url.getRecommendB, _obj).then(
data => {
if (data.code == 0) {
if (this.getRecommendBList.length == 0) {
this.getRecommendBList = data.obj;
} else {
this.getRecommendBList.concat(data.obj);
}
}
},
err => {
alert("网络缓慢。。");
}
);
},

getVideoDistri() {
let _obj = {
openId: url.openId,
name: this.itemName,
size: this.page.size,
current: this.page.current
};
this.$fetch.post(url.getVideoDistri, _obj).then(
data => {
if (data.code == 0) {
if (this.getVideoDistriList.length == 0) {
this.getVideoDistriList = data.obj;
} else {
this.getVideoDistriList.concat(data.obj);
}
}
},
err => {
alert("网络缓慢。。");
}
);
},

getOrderDistri() {
let _obj = {
openId: url.openId,
name: this.itemName,
size: this.page.size,
current: this.page.current
};
this.$fetch.post(url.getOrderDistri, _obj).then(
data => {
if (data.code == 0) {
if (this.getOrderDistriList.length == 0) {
this.getOrderDistriList = data.obj;
} else {
this.getOrderDistriList.concat(data.obj);
}
}
},
err => {
alert("网络缓慢。。");
}
);
},

selPullUp() {
this.page.current++;
this.getListF();
},

pulldown() {
this.isUnMore1 = false;
this.page.current = 1;
this.getListF();
},

getListF() {
switch (this.routeParams.data.id) {
case "001":
this.getRecommendB();
break;
case "002":
this.getVideoDistri();
break;
case "003":
this.getOrderDistri();
break;
}
}
},
created() {
settitle("健康奖金详情");
this.routeParams = JSON.parse(this.$route.params.obj);
this.getListF(); 
},

mounted() {}
};



