import url from "../../bin/url";
export default {
  name: "exclusive",
  data() {
    return {};
  },
  methods: {
    // 门店更换
    goToNationalStores(){
      this.$router.push({
        name: "nationalStores",
        params: {
          obj: JSON.stringify({
            type: "profession",
            data: {
              id: "蚕丝"
            }
          })
        }
      });
    }
  },
  created() {
    settitle("我是专属门店");
    this.routeParams = JSON.parse(this.$route.params.obj);
  },

  mounted() {
    console.log("专属门店");
  }
};



