import url from "../../bin/url";
export default {
  name: "caseVideo",
  data() {
    return {
      videoOneList: "", //列表
      videoId: ""
    };
  },
  methods: {
    // 去视频列表
    goToVodeL(item) {
      this.$router.push({
        path: "/videolist",
        query: {
          obj: JSON.stringify({
            type: "profession",
            data: {
              id: item
            }
          })
        }
      });
    },
    // 去视频列表
    goToVOrder(item) {
      this.$router.push({
        path: "/videosorder"
        //  query: {
        //   obj: JSON.stringify({
        //     type: "profession",
        //     data: {
        //       id: item
        //     }
        //   })
        // }
      });
    },
    // 获取列表
    getVideoOne() {
      let _obj = {
        openId: url.openId
      };
      this.$fetch.post(url.getVideoOne, _obj).then(
        data => {
          if (data.code == 0) {
            this.videoOneList = data.obj;
          }
        },
        err => {
          alert("网络缓慢。。");
        }
      );
    }
  },

  mounted() {
    this.getVideoOne(); //获取视频
  }
};



