
import url from "../../bin/url";
import Confirm from 'vux/src/components/confirm/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

export default {
components: {
Confirm,
XDialog
},
name: "memberD",
data() {
return {
classA: "1",
memberFalg: "", //辟谷/会员套餐标记
payShowD: false, //支付
ni: "",
payBFalge: false, //辟谷消费提示
parameter: "", //参数
infoList: "", //详情
nowTime: new Date(), //当前日期
priceHT: "", //会员套餐价格
orderOverFalge: false, //结束标示
msgPAW: "",
msgLength: 0
};
},
watch: {
msgPAW(curVal) {
console.log(curVal);
if (/[^\d]/g.test(curVal)) {
this.msgPAW = this.msgPAW.replace(/[^\d]/g, "");
} else {
this.msgLength = curVal.length;
this.msg = DesUtils.encode(curVal, "fruits-app,yuntu,com");
}
if(this.msgLength == 6){
console.log('我来了')
this.getSaveMember()
this.payShowD =false
}
}
},
methods: {
focus() {
this.$refs.pwd.focus();
},

tabT(item) {
this.classA = item.id;
this.priceHT = item.retailPrice;
},

goOver() {
this.orderOverFalge = true;
},

onCancelOver() {},

onConfirmOVer() {
let _obj = {
openId: url.openId,
id: this.parameter.item.id
};
this.$fetch.post(url.closeMemberOrder, _obj).then(
data => {
if (data.code == 0) {
console.log("会员订单结束");
}
},
err => {
alert("网络缓慢。。");
}
);
},

goPay() {
this.payShowD = true;
},

goPayB(id) {
this.payId = id; 
this.payBFalge = true;
},

onCancel() {},

onConfirm() {
let _obj = {
openId: url.openId,
id: this.payId
};
this.$fetch.post(url.sureBigu, _obj).then(
data => {
if (data.code == 0) {
this.getInfo(this.parameter.item.id);
}
},
err => {
alert("网络缓慢。。");
}
);

},

getInfo(id) {
let _obj = {
openId: url.openId,
id: id
};
this.$fetch.post(url.getOrderInfo, _obj).then(
data => {
if (data.code == 0) {
this.infoList = data.obj;
console.log(data);
}
},
err => {
alert("网络缓慢。。");
}
);
},

timeNow() {
var now = new Date(); 
var nowDay = now.getDate(); 
var nowMonth = now.getMonth(); 
var nowYear = now.getFullYear(); 
this.nowTime = nowYear + "." + nowMonth + "." + nowDay;
},

getSaveMember() {
let _obj = {
openId: url.openId,
id: this.parameter.item.id,
cupId: this.classA,
password:  this.msg
};
this.$fetch.post(url.saveMember, _obj).then(
data => {
if (data.code == 0) {
this.infoList = data.obj;
console.log(data);
}
},
err => {
alert("网络缓慢。。");
}
);
}
},
created() {
settitle("会员操作详情");
this.parameter = this.routeParams = JSON.parse(this.$route.params.obj).data;
console.log(this.parameter.item.type + "套餐类型");
if (this.parameter.item.type == "1") {
this.memberFalg = false; 
} else {
this.memberFalg = true; 
}
},

mounted() {
this.getInfo(this.parameter.item.id); 
this.timeNow();
}
};



