
import url from "../../bin/url";
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
directives: {
TransferDom
},
components: {
Popup,
XButton,

chooseNum: resolve => require(["./choosenum/index.vue"], resolve)
},
data() {
return {
show: false,
list: [1, 2, 3],
videoList: "",
videoListObj: "",
videoIdPay: "",
modelData: "",
videolUrl:''
};
},
methods: {

getVideoList() {
let _obj = {
openId: url.openId,
id: this.videoId
};
this.$fetch.post(url.getVideoList, _obj).then(
data => {
if (data.code == 0) {
this.videoList = data.attributes;
this.videoListObj = data.obj;
}
},
err => {
alert("网络缓慢。。");
}
);
},

saveVideoOrder() {
let _obj = {
openId: url.openId,
id: this.videoIdPay
};
this.$fetch.post(url.saveVideoOrder, _obj).then(
data => {
if (data.code == 0) {
console.log(data)
}
},
err => {
alert("网络缓慢。。");
}
);
},

openBuy(item) {
this.videoIdPay = item.id;
let _obj = {
openId: url.openId,
id: this.videoIdPay
};
this.$fetch.post(url.getVideoLink, _obj).then(
data => {
if (data.code == 0) {
this.modelData = data.obj;
if (data.obj.state == 0) {
this.show = true;
} else {
this.videolUrl =
"http://player.youku.com/embed/XNDEyMTAwNjE0MA==";
}
}
},
err => {
alert("网络缓慢。。");
}
);
}
},
mounted() {
this.getVideoList();
},
created() {
this.routeParams = JSON.parse(this.$route.query.obj);
console.log("567890-");
this.videoId = this.routeParams.data.id;
}
};



